import React from 'react'
import Button from '../../Components/Ui/Button'
import Input from '../../Components/Ui/Input/Input'
import is from 'is_js'
import classes from './Auth.css'
// import axios from 'axios'
import {connect} from 'react-redux'
import {auth} from '../../store/actions/auth'

class Auth extends React.Component{
	state={
		isFormValid: false,
		formControls:{
			email: {
				label: 'Email',
				type: 'email',
				value: "",
				valid: false,
				touched: false,
				errorMessage: 'Введите коректный Email',
				validation: {
					required: true,
					email: true
				}
			},
			password: {
				label: 'Пароль',
				type: 'password',
				value: "",
				valid: false,
				touched: false,
				errorMessage: 'Введите коректный пароль',
				validation: {
					required: true,
					minLength: 6
				}
			}
		}
	}
	formHandler = (e) =>{
		e.preventDefault();
	}

	onChangeHandler = (event, controlName)=>{
		console.log(`${controlName}`, event.target.value)
		const formControls = {...this.state.formControls}
		const control = {...formControls[controlName]}

		control.value = event.target.value;
		control.touched = true;
		control.valid = this.validateControl(control.value, control.validation)
		

		formControls[controlName] = control;

		let isFormValid = true

		Object.keys(formControls).forEach((name)=>{
			isFormValid = formControls[name].valid && isFormValid

		})

		this.setState({
			formControls: formControls,
			isFormValid: isFormValid
		})


	}
	validateControl = (value, validation)=>{
		if(!validation){
			return true
		}
		let isValid = true

		if(validation.required){
			isValid = value.trim() !== '' && isValid
		}
		if(validation.email){
			isValid = is.email(value) && isValid
		}
		if(validation.minLength){
			isValid = value.length >= 6 && isValid 
		}
		return isValid
	}

	renderInputs = () =>{
		return Object.keys(this.state.formControls).map((controlName, index)=>{
			const control = this.state.formControls[controlName]
			return(
				<Input
					key={controlName + index}
					label={control.label}
					type={control.type}
					value={control.value}
					valid={control.valid}
					touched={control.touched}
					errorMessage={control.errorMessage}
					shouldValidate={!!control.validation}
					onChange={(event) => this.onChangeHandler(event, controlName)}
				/>
			)
		})
	}
	loginHandler = () =>{
		// 	const authData = {
		// 	email: this.state.formControls.email.value,
		// 	password: this.state.formControls.password.value,
		// 	returnSecureToken: true
		// }
		this.props.auth(this.state.formControls.email.value, this.state.formControls.password.value, true)
		// try {
		// 	// const response = await axios.post('https://www.googleapis.com/identitytoolkit/v3/relyingparty/verifyPassword?key=AIzaSyD6I0QeA53XgNEB2tBfoWWdmD-kaIJMdmA', authData)
		// 	console.log(response.data)
		// } catch(e) {
		// 	console.log(e);
		// }
	}
	registerHandler = async () =>{
		// const authData = {
		// 	email: this.state.formControls.email.value,
		// 	password: this.state.formControls.password.value,
		// 	returnSecureToken: true
		// }
		this.props.auth(this.state.formControls.email.value, this.state.formControls.password.value, true)
		// try {
		// 	const response = await axios.post('https://www.googleapis.com/identitytoolkit/v3/relyingparty/signupNewUser?key=AIzaSyD6I0QeA53XgNEB2tBfoWWdmD-kaIJMdmA', authData)
		// 	console.log(response.data)
		// } catch(e) {
		// 	console.log(e);
		// }
	}
	render(){
		return(
		<div className={classes.Auth}>
			<div>
				<h1>Authentification</h1>
				<form onSubmit={this.formHandler}>
					{this.renderInputs()}
					<Button
						onClick={this.loginHandler}
						types='success'
						disabled={!this.state.isFormValid}
					>
						Войти
					</Button>
					<Button
						onClick={this.registerHandler}
						types='primary'
						disabled={!this.state.isFormValid}
					>
						Зарегистрироваться
					</Button>
				</form>
			</div>
		</div>
		)
	}
}




function mapDispatchToProps(dispatch){
	return{
		auth: (email, password, isLogin) => dispatch(auth(email, password, isLogin))
	}
}
export default connect(null, mapDispatchToProps)(Auth)