import axios from 'axios'
import {FETCH_QUIZES_START, FETCH_QUIZES_SUCCESS, FETCH_QUIZES_ERROR, FETCH_QUIZ_SUCCESS, QUIZ_SET_STATE, FINISH_QUIZ, NEXT_ANSWER_QUESTION, REPEAT_HANDLER} from './actionTypes'


export function fetchQuizById(quizId){
	return async dispatch => {
		dispatch(fetchQuizesStart())

		try {
			const response = await axios.get('https://react-quiz2.firebaseio.com/quizes/' + quizId + '.json')
			const quiz = response.data
			// this.setState({
			// 	quiz: quizes,
			// 	loader: false
			// })
			dispatch(fetchQuizSuccess(quiz))
		} catch(e) {
			dispatch(fetchQuizesError(e))
		}
	}
}


export function fetchQuizes(){
	return async dispatch => {
		dispatch(fetchQuizesStart())
		try {
			const response = await axios.get('https://react-quiz2.firebaseio.com/quizes.json')
			console.log(response)
			const quizes = []
			Object.keys(response.data).forEach((key, index)=>{
				quizes.push({
					id: key,
					name: `Тест №${index + 1}`
				})
			})
			dispatch(fetchQuizesSuccess(quizes))
		
		} catch(e) {
			dispatch(fetchQuizesError(e))
		}
	}
}

export function fetchQuizSuccess(quiz){
	return{
		type: FETCH_QUIZ_SUCCESS,
		quiz
	}
}

export function fetchQuizesStart(){
	return{
		type: FETCH_QUIZES_START
	}
}

export function fetchQuizesSuccess(quizes){
	return{
		type: FETCH_QUIZES_SUCCESS,
		quizes: quizes
	}
}

export function fetchQuizesError(e){
	return{
		type: FETCH_QUIZES_ERROR,
		error: e

	}
}

export function quizSetState(answerState, results){
	return{
		type: QUIZ_SET_STATE,
		answerState,
		results	
	}
	
}

export function finishQuiz(){
	return{
	type: FINISH_QUIZ
	}
}
export function nextAnswerQuestion(number){
	return{
		type: NEXT_ANSWER_QUESTION,
		number
	}
}
function isQuizFinished(state){
	return state.quiz.length === state.activeQuestion + 1
}
export function repeatHandler(){
	return{
		type: REPEAT_HANDLER
	}
}

export function quizAnswerClick(answerId){
	return (dispatch, getState) => {
	const state = getState().quiz
	if(state.answerState){
		const key = Object.keys(state.answerState)[0]
		if(state.answerState[key] === 'success' || state.answerState[key] === 'error'){
			return
		}
	}
	const question = state.quiz[state.activeQuestion]
	const results = state.results

	if(+question.rightAnswerId === answerId){
		if(!results[question.id]){
			results[question.id] = 'success'
		}

		dispatch(quizSetState({[answerId]: 'success'}, results))

		const timeout = window.setTimeout(()=>{
			if(isQuizFinished(state)){
				
				dispatch(finishQuiz())
			}
			else{

				dispatch(nextAnswerQuestion(state.activeQuestion + 1))
			}
		window.clearTimeout(timeout)
		}, 1000)

		
	}
	else{
		results[question.id] = 'error'

		dispatch(quizSetState({[answerId]: 'error'}, results))
		const timeout = window.setTimeout(()=>{
			if(isQuizFinished(state)){
				dispatch(finishQuiz())
			}
			else{
				dispatch(nextAnswerQuestion(state.activeQuestion + 1))
			}
		window.clearTimeout(timeout)
		}, 1000)
	}
	}
	
}

