import {CREATE_QUIZ_QUESTION, RESET_QUIZES} from '../actions/actionTypes'
const initialState = {
	quiz: []
}
export default function quizCreator(state = initialState, action){
	switch (action.type) {
		case CREATE_QUIZ_QUESTION:
			return{
				...state,
				quiz: [...state.quiz, action.item]
			}
		case RESET_QUIZES:
			return{
				...state,
				quiz: []
			}
		default:
			return state
	}
}

