import axios from 'axios'
import {CREATE_QUIZ_QUESTION, RESET_QUIZES} from './actionTypes'
export function createQuizQuestion(item){
	return {
		type: CREATE_QUIZ_QUESTION,
		item
	}
}

export function resetQuizes(){
	return {
		type: RESET_QUIZES
	}
}

export function finishCreateQuiz(){
	return async (dispatch, getState) => {
		await axios.post('https://react-quiz2.firebaseio.com/quizes.json', getState().creator.quiz)
		dispatch(resetQuizes())
	}
}