import React from 'react'
import Input from '../../Components/Ui/Input/Input'
import Button from '../../Components/Ui/Button'
import {createControl, validate, validateForm} from '../../form/formFramework'
import {Auxillary} from '../../ho/Auxillary/Auxillary'
import Select from '../../Components/Ui/Select/Select'
// import axios from 'axios'
import classes from './QuizCreator.css'
import {connect} from 'react-redux'
import {createQuizQuestion, finishCreateQuiz} from '../../store/actions/creator'

function createOptionControl(number) {
	return createControl({
				label: `Вариант ${number}`,
				errorMessage: 'Поле не может быть пустым',
				id: number
			}, {required: true})
}

function createFormControl(){
	return {
		question: createControl({
				label: 'Введите вопрос',
				errorMessage: 'Поле не может быть пустым'
			}, {required: true}),
			option1: createOptionControl(1),
			option2: createOptionControl(2),
			option3: createOptionControl(3),
			option4: createOptionControl(4)
	}
}

class QuizCreator extends React.Component{
	state={
		rightAnswerId: 1,
		isFormValid: false,
		formControls: createFormControl()
	}
	onChangeHandler = (value, controlName)=>{
		const formControls = {...this.state.formControls}
		const control = {...this.state.formControls[controlName]}

		control.value = value
		control.touched = true
		control.valid = validate(control.value, control.validation)

		formControls[controlName] = control

		this.setState({
			formControls: formControls,
			isFormValid: validateForm(formControls)
		})

	}
	renderInputs = () =>{
	return Object.keys(this.state.formControls).map((controlName, index)=>{
		const control = this.state.formControls[controlName];
		return(
			<Auxillary
				key={index}
			>
				<Input
					label={control.label}
					errorMessage={control.errorMessage}
					value={control.value}
					shouldValidate={!!control.validation}
					touched={control.touched}
					valid={control.valid}
					onChange={(event)=>this.onChangeHandler(event.target.value, controlName)}
				/>
				{index === 0 ? <hr/> : null}
			</Auxillary>
		)

	})
}

	onChangeHandlers = (event) =>{
		this.setState({
			rightAnswerId: event.target.value
		})
	}

	onAddHandler = (event) => {
		event.preventDefault()
		const {question, option1, option2, option3, option4} = this.state.formControls
		const questionItem = {
			question: question.value,
			id: this.props.quiz.length + 1,
			rightAnswerId: this.state.rightAnswerId,
			answers: [
				{text: option1.value, id: option1.id},
				{text: option2.value, id: option2.id},
				{text: option3.value, id: option3.id},
				{text: option4.value, id: option4.id}
			]
		}
		this.props.createQuizQuestion(questionItem)
		this.setState({
			rightAnswerId: 1,
			isFormValid: false,
			formControls: createFormControl()
		})
			
	}
	submitHandler(event){
		event.preventDefault();
	}
	onCreateHandler = (event) => {
		event.preventDefault();
		this.setState({
			rightAnswerId: 1,
			isFormValid: false,
			formControls: createFormControl()
		})
		this.props.finishCreateQuiz()
	}
	render(){
		const select = 
		<Select
			label={'Выберите правильный ответ'}
			value={this.state.rightAnswerId}
			options={[
				{text: 1, value: 1},
				{text: 2, value: 2},
				{text: 3, value: 3},
				{text: 4, value: 4}
			]}
			onChange={this.onChangeHandlers}
		/>
		return(
		<div className={classes.QuizCreator}>
			<h1>QuizCreator</h1>
			
			<form onSubmit={this.submitHandler}>
				{this.renderInputs()}
				{select}
				<Button
					onClick={this.onAddHandler}
					types="primary"
					disabled={!this.state.isFormValid}
				>Добавить вопрос</Button>
				<Button
					onClick={this.onCreateHandler}
					types="success"
					disabled={this.props.quiz.length === 0}
				>Создать тест</Button>
			</form>
		
		</div>
		)
	}
}
function mapStateToProps(state){
	return{
		quiz: state.creator.quiz
	}
}
function mapDispatchToProps(dispatch){
	return{
		createQuizQuestion: (item) => {dispatch(createQuizQuestion(item))},
		finishCreateQuiz: () => {dispatch(finishCreateQuiz())}

	}
}

export default connect(mapStateToProps, mapDispatchToProps)(QuizCreator)