import React from 'react'
import {NavLink} from 'react-router-dom'
import classes from './Drawer.css'




class Drawer extends React.Component{
	renderLinks = (links) =>{
		return links.map((link, index) =>{
			return(
				<li key={index}>
					<NavLink
						to={link.path}
						exact={link.exact}
						onClick={this.props.anchorHandler}
					>
						{link.label}
					</NavLink>
				</li>
			)
		})
	}

	render(){
		const cls = [
			classes.Drawer
		]	 
		if(!this.props.isOpen){
			cls.push(classes.close)
		}

		const links = [
			{path: '/', label: "Список тестов", exact: true}
			
		]
		if(this.props.isAuthenticated){
			links.push({path: '/quiz-creator', label: "Создание теста", exact: false})	
			links.push({path: '/logout', label: "Выйти", exact: false})	
		} else{
			links.push({path: '/auth', label: "Аутентификация", exact: false})	
		}
		return(
			<nav className={cls.join(' ')}>
				<ul>
					{this.renderLinks(links)}
				</ul>
			</nav>
		)
	}
}
export default Drawer